<style lang="scss">
.link-page {
  a {
    color: #333;
  }
}
</style>

<template>
  <span class="link-page">
    <a v-if="mode == 'design' || mode == 'preview'"><slot></slot></a>

    <template v-else>
      <!-- router -->
      <nuxt-link v-if="to && !isTargetLink(to)" :to="to"><slot></slot></nuxt-link>
      <!-- router -->
      <a v-if="to && isTargetLink(to)" :href="to" target="_bank"><slot></slot></a>
      <!--  -->
      <a v-if="!to"><slot></slot></a>
    </template>
  </span>
</template>

<script>
import mixins from '../mixins'
export default {
  name: 'linkPage',
  mixins: [mixins],
  props: {
    to: String
  },
  data() {
    return {}
  },
  methods: {
    isTargetLink(to) {
      if (/^(http|https)/.test(to)) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>
